/* main-menu.css */
.main-menu {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
    background-color: #f7f7f7; /* Changed to match the overall theme */
    color: #333;
  }
  
  .title {
    font-size: 48px;
    font-family: 'Arial', sans-serif;
    margin-bottom: 40px;
    text-transform: uppercase;
    letter-spacing: 3px;
    color: #333; /* Matched title color to the overall theme */
  }
  
  .menu-buttons {
    display: flex;
    flex-direction: column;
    width: 300px;
  }
  
  .menu-button {
    background-color: #ffeb3b;
    color: #333;
    border: none;
    padding: 15px;
    margin: 10px 0;
    font-size: 24px;
    cursor: pointer;
    border-radius: 5px;
    transition: background-color 0.3s ease;
  }
  
  .menu-button:hover {
    background-color: #ffd600;
  }
  
  .menu-button:disabled {
    background-color: #ddd;
    color: #999;
    cursor: not-allowed;
  }
  
  .high-score {
    margin-top: 40px;
  }
  
  .high-score h2 {
    font-size: 24px;
    font-family: 'Arial', sans-serif;
    color: #333;
  }
  