/* Basic styling for container */
.container {
    text-align: center;
    margin: 20px;
    max-width: 100%; /* Ensures the game fits well on smaller screens */
    padding: 0 10px;
  }
  
  /* Game management button section */
  .game-management {
    display: flex;
    justify-content: space-between;
    margin-bottom: 30px;
    padding: 10px 0;
  }
  
  /* Styling for buttons */
  .game-management button, 
  .controls button {
    padding: 10px 20px;
    font-size: 18px;
    background-color: #ffeb3b;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    margin: 5px;
  }
  
  button:hover {
    background-color: #ffd600;
  }
  
  /* Grid for the letters */
  .letter-grid {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    gap: 10px;
    margin-bottom: 20px;
  }
  
  .letter {
    width: 60px;
    height: 60px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    background-color: lightgray;
    font-size: 24px;
    font-weight: bold;
    color: #333;
    cursor: pointer;
  }
  
  .letter.central {
    background-color: #ffeb3b;
    color: black;
  }
  
  /* Input field styling */
  input[type="text"] {
    padding: 10px;
    font-size: 18px;
    width: 250px;
    margin-bottom: 20px;
    border-radius: 5px;
    border: 1px solid #ccc;
  }
  
  /* Responsive design for smaller screens */
  @media (max-width: 768px) {
    .letter {
      width: 50px;
      height: 50px;
      font-size: 20px;
    }
  
    input[type="text"] {
      width: 200px;
    }
  
    .controls button {
      font-size: 16px;
      padding: 8px 16px;
    }
  }
  
  /* Word info section for number of possible words */
  .word-info {
    font-size: 18px;
    color: #333;
    margin-bottom: 20px;
  }
  
  /* Found words container with fixed height and scroll */
  .found-words-container {
    margin-top: 10px;
    padding: 15px;
    border: 1px solid #ccc;
    border-radius: 5px;
    background-color: #f7f7f7;
    text-align: left;
    margin-bottom: 20px;
    height: 150px; /* Fixed height */
    overflow-y: auto; /* Scrollable content */
  }
  
  .found-words-container h2 {
    margin-bottom: 10px;
    font-size: 20px;
  }
  
  /* Styling for word list */
  .word-list {
    list-style-type: none;
    padding-left: 0;
    font-size: 16px;
  }
  
  .word-list li {
    padding: 5px 0;
    border-bottom: 1px solid #ddd;
    color: #555;
  }
  
  /* Controls section for buttons below the grid */
  .controls {
    margin-top: 20px;
  }
  
  /* Score styling */
  .score {
    font-size: 24px;
    color: #333;
  }
  